.block-social {
  display: flex;
  justify-content: flex-start;
  &__item {
    margin-right: $gap-150;
    &:last-child {
      margin-right: 0; }
    img {
      height: 75px;
      @include r(1921) {
        height: 68px; }
      @include r(1023) {
        height: 65px; }
      @include r(767) {
        height: 50px; } } } }
