.section {
  &__container {
    padding: 160px 0 120px;
    height: 100vh;
    @include r(1921) {
      padding: 130px 0 $gap-900; }
    @include r(1365) {
      padding: 130px 0 $gap-500; }
    @include b(750) {
      height: auto;
      padding: $gap-500 0; }
    @include r(767) {
      height: auto;
      padding: $gap-500 0; } } }
