.button {
  min-width: 200px;
  width: auto;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: $gap-150 $gap-250;
  text-align: center;
  border-radius: $gap-300;
  @include rm;
  font-size: rem(16);
  box-shadow: $shadow-100;
  transition: box-shadow $transition-100;
  @include r(1023) {
    min-width: 150px; }
  @include r(767) {
    min-width: 120px;
    margin-right: $gap-150;
    &:last-child {
      margin-right: 0; } }
  &--medium {
    height: 56px;
    @include r(1023) {
      height: 42px; } }
  &--primary {
    background: $gradient-400;
    color: $white; }
  &:hover {
    &:not(.button--disabled) {
      box-shadow: $shadow-500; } }
  &--disabled {
    background-color: rgba($blue-100, $opacity-30);
    color: $blue-1000;
    cursor: not-allowed; } }
