.main {
  background-color: $white-100;
  position: relative;
  &--bg {
    background-color: transparent;
    &::before {
      content: '';
      position: fixed;
      z-index: $z-index-0;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      display: block;
      background-image: url('../img/background.svg');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50%; } } }
