.more-list {
  &__container {
    padding: $gap-300 0 120px;
    @include r(1023) {
      padding: $gap-300 0 80px; }
    @include r(767) {
      padding: $gap-300 0 50px; } }
  &__headline {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: $gap-650; }
  &__headline-icon {
    width: 115px;
    height: 115px;
    margin-right: $gap-250;
    @include r(1023) {
      width: 75px;
      height: 75px; }
    @include r(767) {
      width: 65px;
      height: 65px; } }
  &__headline-title {
    font-size: rem(48);
    @include rb;
    @include r(767) {
      font-size: rem(23); } }
  &__tile {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: flex-start;
    margin: -$gap-200; }
  &__tile-item {
    width: calc(100% / 3);
    padding: $gap-200;
    @include r(1023) {
      width: 50%; }
    @include r(767) {
      width: 100%;
      padding: $gap-200 $gap-200 $gap-500;
      &:last-child {
        padding-bottom: $gap-200; } } }
  &__tile-item-wrapper {
    display: block;
    width: 100%;
    height: 100%; }
  &__tile-item-illustration {
    width: 100%;
    height: 380px;
    overflow: hidden;
    border-radius: $gap-200;
    margin-bottom: $gap-400;
    @include r(767) {
      height: 200px; }
    picture,
    img {
      height: 100%;
      width: 100%; }
    img {
      object-fit: cover; } }
  &__tile-item-title {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    margin-bottom: $gap-300; }
  &__tile-item-artifacts {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap; }
  &__tile-item-date,
  &__tile-item-author {
    font-size: rem(22);
    @include rm; }
  &__tile-item-date {
    color: $blue-500;
    margin-right: $gap-500; }
  &__tile-item-author {
    color: $blue-100; } }
