.blog {
  &__container {
    min-height: 100vh;
    padding: 155px 0 120px;
    @include r(1921) {
      padding: 155px 0 $gap-900; }
    @include r(1023) {
      padding: $gap-900 0 $gap-900; } }
  &__header {
    display: flex;
    justify-content: center;
    margin-bottom: $gap-350;
    @include r(1023) {
      justify-content: flex-start; } }
  &__subheader {
    margin-bottom: $gap-350;
    width: 100%; }
  &__search {
    position: relative;
    display: block; }
  &__search-input {
    width: 100%;
    border: 0;
    padding: $gap-100 $gap-900 $gap-100 $gap-100;
    border-bottom: 1px solid rgba($blue-100, $opacity-50);
    background-color: transparent;
    @include rbl;
    font-size: rem(27);
    color: $blue-100;
    outline: 0;
    @include r(1023) {
      padding: $gap-100 $gap-500 $gap-100 $gap-100; }
    &::placeholder {
      @include rr;
      font-size: rem(27);
      color: $blue-100; } }
  &__search-icon {
    border: 0;
    width: 30px;
    height: 30px;
    position: absolute;
    top: 50%;
    right: 50px;
    transform: translateY(-50%);
    z-index: $z-index-1;
    padding: $gap-50;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    @include r(1023) {
      right: 15px; }
    img {
      width: 100%;
      height: 100%; } }
  &__content {
    position: relative;
    &::after {
      content: "";
      top: 0;
      left: 0;
      display: block;
      position: absolute;
      z-index: $z-index-1;
      width: 100%;
      height: 100%;
      backdrop-filter: blur(4px);
      background-color: rgba($white-100, $opacity-70);
      opacity: 0;
      pointer-events: none;
      transition: opacity $transition-100; }
    &--blur {
      &::after {
        pointer-events: auto;
        opacity: 1; } } }
  &__tile {
    margin-bottom: $gap-900; }
  &__article {
    margin-bottom: $gap-500;
    &:last-child {
      margin-bottom: 0; } }
  &__article-title {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal; }
  &__article-container {
    width: 100%;
    height: 380px;
    display: flex;
    align-items: stretch;
    justify-content: flex-start;
    @include r(767) {
      flex-direction: column;
      max-width: 550px;
      width: 100%;
      height: auto;
      margin: 0 auto; } }
  &__article-illustration {
    width: 50%;
    border-radius: $gap-200;
    overflow: hidden;
    flex-shrink: 0;
    @include r(767) {
      width: 100%;
      height: 270px; }
    @include r(479) {
      height: 220px; }
    picture,
    img {
      height: 100%; }
    img {
      width: 100%;
      object-fit: cover;
      object-position: 50% 50%; } }
  &__article-content {
    padding: $gap-350 $gap-500;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    @include r(767) {
      padding: $gap-250; } }
  &__article-title {
    margin-bottom: $gap-350; }
  &__article-artifacts {
    margin-top: auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap; }
  &__article-date,
  &__article-author {
    font-size: rem(23);
    @include rm;
    @include r(767) {
      font-size: rem(16); } }
  &__article-date {
    color: $blue-500;
    margin-right: $gap-250; }
  &__article-author {
    color: $blue-100; }
  &__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    @include r(767) {
      justify-content: center; } }
  &__navigation-button {
    &--prev {
      @include r(767) {
        order: 1; } }
    &--next {
      @include r(767) {
        order: 2; } } }
  &__pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    @include r(767) {
      width: 100%;
      order: 3;
      padding-top: $gap-350; } }
  &__pagination-item {
    width: 60px;
    height: 60px;
    border-radius: $gap-100;
    border: 1px solid rgba($blue-100, $opacity-30);
    margin-right: $gap-100;
    overflow: hidden;
    @include r(1023) {
      width: 40px;
      height: 40px; }
    @include r(767) {
      width: 35px;
      height: 35px; }
    &:last-child {
      margin-right: 0; } }
  &__pagination-item-link {
    @include rbl;
    color: $blue-1000;
    width: 100%;
    height: 100%;
    padding: $gap-100;
    display: flex;
    align-items: center;
    justify-content: center;
    &--active {
      color: $white;
      background-color: $blue-500; } }
  &__result {
    padding-top: $gap-600;
    margin: 0 auto;
    max-width: 600px;
    width: 100%;
    @include r(1023) {
      padding-top: $gap-300; } }
  &__result-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: $gap-700;
    @include r(1023) {
      margin-bottom: $gap-450; } }
  &__result-string {
    text-align: center;
    @include rb;
    font-size: rem(30);
    margin-bottom: $gap-300;
    @include r(1023) {
      font-size: rem(24); }
    @include r(767) {
      font-size: rem(18); }
    &:last-child {
      margin-bottom: 0; }
    span {
      display: block; } } }



