.block-typography {
  color: $text-black;

  b {
    @include rb; }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    padding-bottom: $gap-450;
    @include r(767) {
      padding-bottom: $gap-300; } }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    @include rm; }

  h1,
  h2,
  h3 {
    font-size: rem(40);
    @include r(1023) {
      font-size: rem(32); }
    @include r(767) {
      font-size: rem(21); } }

  h4 {
    font-size: rem(37);
    @include r(1023) {
      font-size: rem(28); }
    @include r(767) {
      font-size: rem(21); } }

  p,
  span,
  li {
    line-height: 1.65em;
    @include rr;
    font-size: rem(28);
    @include r(1919) {
      font-size: rem(24); }
    @include r(767) {
      font-size: rem(18); } }

  blockquote {
    padding: $gap-400 140px;
    margin-bottom: $gap-450;
    position: relative;
    font-size: rem(36);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include r(1919) {
      padding: $gap-400 100px; }
    @include r(1365) {
      padding: $gap-400 $gap-500 $gap-400 $gap-800; }
    @include r(1023) {
      padding: $gap-400 $gap-400 $gap-400 $gap-500;
      font-size: rem(24);
      margin-bottom: $gap-250; }
    @include r(767) {
      padding: $gap-200 $gap-200 $gap-200 $gap-450; }
    &::before {
      content: "";
      position: absolute;
      top: $gap-450;
      left: 60px;
      width: 60px;
      height: 35px;
      display: block;
      background-image: url('../img/blockquote.svg');
      background-size: contain;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      @include r(1919) {
        left: 25px; }
      @include r(1365) {
        left: 0;
        width: 50px; }
      @include r(1023) {
        width: 35px; }
      @include r(767) {
        top: $gap-200; } }
    p {
      @include rmi; } }

  cite {
    position: relative;
    padding: 0 0 0 $gap-250;
    @include rm;
    font-size: rem(28);
    font-style: normal;
    display: block;
    text-align: right;
    margin: 0 0 0 auto;
    @include r(1919) {
      font-size: rem(24); }
    @include r(1023) {
      font-size: rem(18); }
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(5px, -50%);
      display: inline-block;
      background-color: $text-black;
      width: 15px;
      height: 3px; } }

  img,
  iframe,
  video {
    height: 535px;
    width: 100%;
    object-fit: cover;
    object-position: 50% 50%;
    margin: $gap-150 0 $gap-450;
    overflow: hidden;
    @include r(1919) {
      height: 400px; }
    @include r(1023) {
      height: 350px; }
    @include r(767) {
      height: 200px; } }

  img,
  video {
    border-radius: $gap-400; }

  dt,
  dd {
    display: inline-block;
    font-size: rem(28);
    @include r(1919) {
      font-size: rem(24); }
    @include r(767) {
      font-size: rem(18); } }

  dt {
    @include rb;
    margin-right: $gap-50; }

  dd {
    @include rr; }

  ul,
  ol {
    padding: $gap-350 $gap-400;
    border-left: 7px solid $pink-500;
    background-color: rgba($pink-100, $opacity-30);
    counter-reset: counter;
    margin-bottom: $gap-450;
    @include r(767) {
      padding: $gap-250 $gap-300; } }

  ol > li {
    &::before {
      content: counter(counter)'.'; } }

  ul > li {
    &::before {
      content: counter(counter, lower-alpha)'.'; } }

  li {
    margin-bottom: $gap-350;
    position: relative;
    &::before {
      counter-increment: counter;
      font-size: rem(28);
      color: $pink-500;
      @include rbl;
      padding-right: $gap-50;
      @include r(1919) {
        font-size: rem(24); }
      @include r(767) {
        font-size: rem(18); } }
    &:last-child {
      margin-bottom: 0; } } }
