.create {
  overflow: hidden;
  &__wrapper {
    position: relative; }
  &__container {
    display: grid;
    grid-template-columns: minmax(0, 1fr);
    justify-items: center;
    grid-template-rows: 1.5fr 3fr;
    @include r(1921) {
      grid-template-rows: 1fr 3fr; }
    @include r(767) {
      grid-template-rows: repeat(2, auto); } }
  &__title {
    margin-bottom: $gap-300;
    text-align: center;
    max-width: 770px;
    width: 100%;
    align-self: center;
    @include r(1921) {
      align-self: flex-start;
      margin-bottom: $gap-600; }
    @include r(1023) {
      text-align: left;
      margin-bottom: $gap-300; } }
  &__media {
    position: relative;
    display: block;
    width: 100%; }
  &__media-inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @include r(1023) {
      position: relative;
      overflow: auto;
      justify-content: flex-start;
      padding-bottom: $gap-200; } }
  &__media-image {
    height: 100%;
    max-height: 600px;
    @include r(1023) {
      padding: 0 $gap-100;
      max-height: 470px; }
    @include r(767) {
      max-height: auto;
      height: 470px; }
    &.image-3 {
      position: relative;
      z-index: $z-index-2; }
    &.image-1,
    &.image-5 {
      z-index: $z-index-0; }
    &.image-2,
    &.image-4 {
      z-index: $z-index-1; }
    &.image-1,
    &.image-2 {
      transform-origin: right; }
    &.image-4,
    &.image-5 {
      transform-origin: left; }
    &.image-1 {
      transform: translateX(125px);
      @include r(1023) {
        transform: translate(0);
        order: 2; }
      .create__media-image-icon {
        top: 0;
        right: 100%;
        width: 230px;
        height: 75px;
        transform: translate(50%, 25%); }
      .create__media-image-picture {
        transform: scale(0.75);
        @include r(1023) {
          transform: scale(1); } } }
    &.image-2 {
      transform: translateX(50px);
      @include r(1023) {
        transform: translate(0);
        order: 3; }
      .create__media-image-icon {
        bottom: 0;
        left: 0;
        width: 85px;
        height: 85px; }
      .create__media-image-picture {
        transform: scale(0.9);
        @include r(1023) {
          transform: scale(1); } } }
    &.image-3 {
      @include r(1023) {
        order: 1; }
      .create__media-image-icon {
        width: 85px;
        height: 85px;
        &:nth-child(1) {
          bottom: 0;
          right: 0;
          transform: translate(50%, 50%); }
        &:nth-child(2) {
          top: 50%;
          left: 0;
          transform: translate(-50%, 25%); } } }
    &.image-4 {
      transform: translateX(-50px);
      @include r(1023) {
        transform: translate(0);
        order: 4; }
      .create__media-image-picture {
        transform: scale(0.9);
        @include r(1023) {
          transform: scale(1); } } }
    &.image-5 {
      transform: translateX(-125px);
      @include r(1023) {
        transform: translate(0);
        order: 5; }
      .create__media-image-icon {
        &:nth-child(1) {
          width: 35px;
          height: 35px;
          top: 50%;
          right: 0;
          transform: translate(25px, 50px); }
        &:nth-child(2) {
          width: 45px;
          height: 45px;
          bottom: 0;
          right: 0;
          transform: translate(-10px, -50px); } }
      .create__media-image-picture {
        transform: scale(0.75);
        @include r(1023) {
          transform: scale(1); } } } }
  &__media-image-inner {
    height: 100%;
    transition: transform $transition-100; }
  &__media-image-picture {
    width: 300px;
    height: 100%;
    @include r(1921) {
      width: 250px; }
    @include r(1023) {
      width: 220px; }
    picture,
    img {
      height: 100%; }
    img {
      object-position: 50% 0%;
      object-fit: cover; } }
  &__media-image-icon {
    position: absolute;
    z-index: $z-index-1;
    @include r(1023) {
      display: none; } }
  &__media-hand {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: $z-index-3;
    filter: drop-shadow($shadow-1200);
    @include r(1023) {
      display: none; }
    img {
      width: 280px;
      height: 660px;
      object-fit: contain; } } }
