.tired {
  &__container {
    display: grid;
    grid-template-columns: minmax(0, 1fr);
    grid-template-rows: 1.5fr 3fr;
    @include r(1921) {
      grid-template-rows: auto 1fr; } }
  &__title {
    align-self: center;
    text-align: center;
    margin-bottom: $gap-500;
    @include r(1921) {
      align-items: flex-start; }
    @include r(1365) {
      margin-bottom: $gap-300; }
    @include r(1023) {
      text-align: left; } }
  &__content {
    position: relative; }
  &__tile {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include r(1023) {
      position: relative;
      overflow: auto;
      padding-bottom: $gap-200; } }
  &__tile-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 $gap-100;
    max-height: 650px;
    height: 100%;
    @include r(1023) {
      max-height: 510px; } }
  &__tile-item-row {
    @include r(1023) {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-bottom: $gap-200; } }
  &__tile-item-icon {
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto $gap-100;
    @include r(1023) {
      margin: 0 $gap-200 0 0;
      flex-shrink: 0; } }
  &__tile-item-title {
    font-size: rem(17);
    @include rb;
    text-align: center;
    margin-bottom: $gap-150;
    @include r(1023) {
      margin-bottom: 0;
      font-size: rem(14); } }
  &__tile-item-image {
    height: 100%;
    @include r(1023) {
      height: 470px; }
    picture,
    img {
      height: 100%; }
    img {
      object-position: 50% 0%;
      object-fit: cover; } } }

