.topic {
  &__container {
    min-height: 100vh;
    padding: 150px 0 120px;
    @include r(1921) {
      padding: 150px 0 $gap-500; }
    @include r(1023) {
      padding: $gap-900 0 0; } }
  &__title {
    margin-bottom: $gap-650;
    text-align: center; }
  &__preview-image {
    height: 535px;
    width: 100%;
    overflow: hidden;
    border-radius: $gap-200;
    margin-bottom: $gap-400;
    @include r(1919) {
      height: 400px; }
    @include r(1023) {
      height: 350px; }
    @include r(767) {
      height: 200px; }
    picture,
    img {
      width: 100%;
      height: 100%; }
    img {
      object-fit: cover;
      object-position: 50% 50%; } }
  &__preview-artifacts {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $gap-450;
    color: $blue-500;
    @include rm;
    font-size: rem(21); }
  &__preview-author,
  &__preview-date {
    overflow: hidden;
    flex-shrink: 0;
    width: 50%;
    overflow: hidden;
    text-overflow: ellipsis; }
  &__preview-author {
    padding-right: $gap-100; }
  &__preview-date {
    padding-left: $gap-100;
    text-align: right; } }
