.policy {
  &__container {
    min-height: 100vh;
    padding: 150px 0 220px;
    @include r(1921) {
      padding: 150px 0; }
    @include r(1023) {
      padding: $gap-900 0 150px; }
    @include r(767) {
      padding: $gap-900 0 $gap-400; } }
  &__content {
    margin-bottom: $gap-800;
    @include r(767) {
      margin-bottom: $gap-200; } }
  &__title {
    text-align: center;
    margin-bottom: $gap-900;
    @include r(1023) {
      margin-bottom: $gap-500; }
    @include r(767) {
      margin-bottom: $gap-350; } }
  &__footer-headline {
    display: flex;
    align-items: center;
    justify-content: flex-start; }
  &__footer-title {
    font-size: rem(48);
    @include rb;
    @include r(767) {
      font-size: rem(23); } }
  &__footer-icon {
    width: 115px;
    height: 115px;
    margin-right: $gap-250;
    @include r(1023) {
      width: 75px;
      height: 75px; }
    @include r(767) {
      width: 65px;
      height: 65px; } } }
